import React, { Component } from "react";
// import cssStyles from "./ToDate.module.css";

class ToDate extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { onClick, value } = this.props;
    return (
      <div onClick={onClick}>
        <div style={{ fontWeight: "bold" }}>END</div>
        {value}
      </div>
    );
  }
}

export default ToDate;



