import React, { Component } from "react";
import ReactMapboxGl, { Layer, Source } from "react-mapbox-gl";
// import cssStyles from "./MapComponent.module.css";

const Map = ReactMapboxGl({
  accessToken:
    "pk.eyJ1IjoibmVsZW5zY2h1dXJtYW5zIiwiYSI6ImhkXzhTdXcifQ.3k2-KAxQdyl5bILh_FioCw"
});

class MapComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstSymbolId: null,
      lat: 5.2679,
      lng: 52.1874,
      zoom: [7]
    };
  }
  render() {
    const { date, time } = this.props;
    const { lat, lng, zoom } = this.state;
    return (
      <Map
        style="mapbox://styles/mapbox/streets-v9" // eslint-disable-line
        center={[lat, lng]}
        zoom={zoom}
        containerStyle={{
          height: "100vh",
          width: "100vw"
        }}
        onStyleLoad={map => {
          var layers = map.getStyle().layers;
          // Find the index of the first symbol layer in the map style
          for (var i = 0; i < layers.length; i++) {
            if (layers[i].type === "symbol") {
              this.setState({
                firstSymbolId: layers[i].id
              });
              break;
            }
          }
        }}
      >
        <Source
          id="precipitation"
          tileJsonSource={{
            type: "raster",
            tiles: [
              `https://demo.lizard.net/api/v3/wms/?SERVICE=WMS&REQUEST=GetMap&VERSION=1.1.1&LAYERS=radar:hour&STYLES=radar-hour&FORMAT=image/png&TRANSPARENT=true&HEIGHT=256&WIDTH=256&TIME=${date.toString()}T${time.toString()}&ZINDEX=20&SRS=EPSG:3857&BBOX={bbox-epsg-3857}`
            ],
            tileSize: 256
          }}
        />
        <Layer
          type="raster"
          id="precipitation"
          sourceId="precipitation"
          before={this.state.firstSymbolId}
        />
      </Map>
    );
  }
}

export default MapComponent;



