import React, { Component } from "react";
// import cssStyles from "./FromDate.module.css";

class FromDate extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { onClick, value } = this.props;
    return (
      <div onClick={onClick}>
        <div style={{ fontWeight: "bold" }}>START</div>
        {value}
      </div>
    );
  }
}

export default FromDate;



