import React, { Component } from "react";

import moment from "moment";
import DatePicker from "react-datepicker";
import FromDate from "./FromDate";
import MapComponent from "./MapComponent";
import ToDate from "./ToDate";
import "react-datepicker/dist/react-datepicker-cssmodules.css";
// import cssStyles from "./First.module.css";



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: moment().format("YYYY-MM-DD"),
      time: moment().format("HH:mm:ss")
    };
  }
  render() {
    const { date, time } = this.state;
    return (
      <div className="App">
        <div
          style={{
            height: 100,
            color: "#ffffff",
            backgroundColor: "#17A085",
            width: "100%",
            position: "absolute",
            bottom: 0,
            zIndex: 3,
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            flexDirection: "row"
          }}
        >
          <div style={{ padding: 20, cursor: "pointer", textAlign: "center" }}>
            <DatePicker
              customInput={<FromDate />}
              showTimeSelect
              timeIntervals={60}
              dateFormat="LLL"
              selected={moment(`${this.state.date}T${this.state.time}`)}
              shouldCloseOnSelect={false}
              maxDate={moment().add(1, "days")}
              onChange={datetime => {
                this.setState({
                  date: datetime.format("YYYY-MM-DD"),
                  time: datetime.format("HH:00:00")
                });
              }}
            />
          </div>
          <div
            style={{
              padding: 20,
              cursor: "pointer",
              textAlign: "center"
            }}
          >
            <DatePicker
              popperPlacement="bottom-end"
              customInput={<ToDate />}
              showTimeSelect
              timeIntervals={60}
              dateFormat="LLL"
              selected={moment(`${this.state.date}T${this.state.time}`)}
              shouldCloseOnSelect={false}
              maxDate={moment().add(1, "days")}
              onChange={datetime => {
                console.log("To date", datetime);
              }}
            />
          </div>
        </div>
        <MapComponent date={date} time={time} />
      </div>
    );
  }
}

export default App;



